<template>
  <div>
    <el-menu
      class="el-menu-vertical-demo"
      :collapse="isCollapse"
      router
      :default-active="$route.path"
      unique-opened
    >
      <el-submenu v-for="item in menu" :index="item.path" :key="item.path">
        <template slot="title">
          <router-link
            tag="li"
            :to="item.children && item.children.length ? '' : item.path"
          >
            <i :class="item.icon"></i>{{ item.title }}</router-link
          >
        </template>
        <template v-if="item.children && item.children.length">
          <menutree :menu="item.children" class="inner_menutree"> </menutree>
        </template>
      </el-submenu>
    </el-menu>
  </div>
</template>

<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 100%;
  float: right;
}
.inner_menutree {
  width: 95%;
  float: right;
}
</style>

<script>
export default {
  name: "menutree",
  props: {
    menu: {
      type: Array,
    },
  },
  data() {
    return {
      isCollapse: false,
    };
  },
  mounted() {},
  methods: {},
};
</script>
<style lang="scss" scoped>
/deep/ .el-menu-item,
/deep/ .el-submenu__title {
  // height: 44px;
  // line-height: 44px;
  // list-style: none;
  // font-size: 5px;
}
</style>



