<template>
  <div class="container" v-drag-resize>
    <div class="left-pane">
      <menutree :menu="$router.options.routes[0].children"></menutree>
    </div>
    <div class="resizer"></div>
    <div class="right-pane">
      <div class="header">
        <span>{{ $t("language") }}</span>
        <el-select v-model="value" placeholder="请选择" @change="selectChange">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div style="height: 90vh; overflow: auto" class="main">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
import menutree from "@/components/menutree.vue";
export default {
  components: { menutree },
  data() {
    return {
      isCollapse: false, //菜单是否收起
      value: "zh",
      options: [
        {
          value: "zh",
          label: "中文",
        },
        {
          value: "en",
          label: "英文",
        },
      ],
    };
  },
  mounted() {},
  methods: {
    selectChange(val) {
      console.log("选择", val);
      this.$i18n.locale = val;
    },
  },
};
</script>
<style>
.header {
  width: 100%;
  height: 7vh;
  background: skyblue;
}

.main::-webkit-scrollbar {
  width: 10px;
}

.main::-webkit-scrollbar-track {
  background: rgb(179, 177, 177);
  border-radius: 10px;
}

.main::-webkit-scrollbar-thumb {
  background: rgb(136, 136, 136);
  border-radius: 10px;
}

.main::-webkit-scrollbar-thumb:hover {
  background: rgb(100, 100, 100);
  border-radius: 10px;
}

.main::-webkit-scrollbar-thumb:active {
  background: rgb(68, 68, 68);
  border-radius: 10px;
}
.container {
  display: flex;
  /* height: 98vh; */
  position: relative;
}
.left-pane {
  /* background-color: lightblue; */
  transition: width 0.2s;
  width: 250px; /* 默认宽度 */
}
.right-pane {
  flex: 1;
  /* background-color: lightcoral; */
  overflow: hidden;
}
.resizer {
  background-color: gray;
  cursor: ew-resize;
  width: 5px;
  height: 90vh;
}
</style>



